.gameItem {
  text-decoration: none;
  color: white;
  width: 320px;
  height: 300px;
  background-color: rgb(36, 37, 41);
  border-radius: 15px;
  cursor: pointer;
  position: relative;
  //margin-bottom: 30px;
  //margin-right: 50px;
  margin: 20px 25px;

}

.gameItem:not(:last-child) {

}

.gameItemTitle {
  font-size: 18px;
  font-weight: bold;
}

.gameItemGenre {
  margin-top: 10px;
  display: flex;
  flex-wrap: wrap;
}

.gameItemDetails{
  padding: 10px 20px;
}

.gameItemDescription {
  margin-top: 10px;
  font-size: 14px;
}

.gameItemBuy {
  position: absolute;
  right: 10px;
  bottom: 10px;
}